
import * as constants from '../constants/reminder';


export function pushNewText(newText) {
	return {
		type: constants.PUSH_NEW_TEXT,
		newText: newText
	}
}

export function clear() {
	return {
		type: constants.CLEAR
	}
}

export function remove(item) {
	return {
		type: constants.DELETE,
		item
	}
}


