import {PUSH_NEW_TEXT,CLEAR,DELETE} from '../constants/reminder'

const initState = {
	texts: []
};

export default function(state=initState, action) {
	switch(action.type) {

		case PUSH_NEW_TEXT:
			let newText = action.newText;

			let oldTexts = state.texts;
			let lastItem = oldTexts[oldTexts.length - 1];
			let newId;
			if(lastItem) {
				newId = lastItem.id + 1;
			}
			else {
				newId = 1;
			}
			let newObj = {
				id: newId,
				text: newText,
				date: new Date().getTime()
			}
			let newTexts = state.texts.concat([newObj]);
			
			return {
				texts: newTexts
			};
			break;

		case CLEAR:
			return {
				texts: []
			}
			break;
		
		case DELETE:
			let index = state.texts.indexOf(action.item);
			state.texts.splice(index,1);
			return state;
			break;
		default:
			return state;
	}
}


