

import React,{Component} from 'react';


import {List, ListItem} from 'material-ui/List';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentSend from 'material-ui/svg-icons/content/send';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import Divider from 'material-ui/Divider';
import ActionInfo from 'material-ui/svg-icons/action/info';

import ActionAndroid from 'material-ui/svg-icons/action/android';

import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import {Card,CardText,CardHeader} from 'material-ui/Card';
import {Popover} from 'material-ui/Popover';
import {Menu,MenuItem} from 'material-ui/Menu';



import * as reminderActions from '../actions/reminder'; 
import reminderStore from '../stores/reminder'; 

import dateFormat from 'dateformat';
import ReactGestures from 'React-gestures';
let store;
export function clearText() {

	store.dispatch(reminderActions.clear());

}

class Reminder extends Component { 

	state = {};

	constructor() {
		super();

		let texts = localStorage['texts'];

		if(texts) {
			texts = JSON.parse(texts);
		}
		this.state.texts = texts || [];

		store = reminderStore(this.state);

		store.subscribe(()=>{
			let state = store.getState();
			this.setState(state);

			localStorage['texts'] = JSON.stringify(state.texts);			
		})
	}

	onSend(e) {
		if(e.type != 'touchend')
			return;

		let newText = this.refs.newText.getValue();

		let action = reminderActions.pushNewText(newText);

		store.dispatch(action);
		// let oldTexts = this.state.texts;
		// let lastItem = oldTexts[oldTexts.length - 1];
		// let newId;
		// if(lastItem) {
		// 	newId = lastItem.id + 1;
		// }
		// else {
		// 	newId = 1;
		// }
		// let newObj = {
		// 	id: newId,
		// 	text: newText
		// }
		// let texts = this.state.texts.concat([newObj]);

	}

	onTouchCard (item, unknown, e){
		if(e.type == 'react-touchend') {
			alert(item.id);
		}
	}
	
	handleTouchTap = (item, event) => {
	  // This prevents ghost click.
	  event.preventDefault();

	  this.setState({
	    popoverOpen: true,
	    anchorEl: event.currentTarget,
	  });

	  this.curCard = item;
	};

	handlePopoverClose () {
	    this.setState({
	      popoverOpen: false,
	    });
	};

	curCard = null;

	handleDeleteItem (item) {
	    let action = reminderActions.remove(item || this.curCard);

	    store.dispatch(action);
	};


	render() {
		const style = {
			send: {
				float: 'right',
				margin: '0 12px 22px 0'
			}
		}

		let items = this.state.texts.map((item)=>{
			let date = new Date(item.date);

			let subtitle = dateFormat(date, 'hh:MM');
			return (
				<ReactGestures
				onSwipeLeft={this.handleDeleteItem.bind(this, item)}>
				<Card className='card' key={item.id}
				onTouchTap={this.handleTouchTap.bind(this,item)}>
					<CardHeader
						title={'#' + item.id}
						subtitle= {subtitle}
					></CardHeader>
					<CardText>{item.text}</CardText>
				</Card>
				</ReactGestures>
				   )
		})
		return (
			<div className="reminder">
				<TextField
				      hintText="Hint Text"
				      multiLine={true}
				      fullWidth={true}
				      ref='newText'
				    />
				 <RaisedButton 
				 label="" 
				 primary={true}
				 style={style.send}
				 icon={<ContentSend />} 
				 onTouchTap={this.onSend.bind(this)}
				 />

				 <div style={{clear:'both'}} >
				 	{items}
				 </div>

				 <Popover
		           open={this.state.popoverOpen}
		           anchorEl={this.state.anchorEl}
		           anchorOrigin={{horizontal: 'left', vertical: 'bottom'}}
		           targetOrigin={{horizontal: 'left', vertical: 'top'}}
		           onRequestClose={this.handlePopoverClose.bind(this)}
		         >
		           <Menu>
		             <MenuItem primaryText="Delete" 
		             	innerDivStyle={{color: 'red'}}
		             	onTouchTap={this.handleDeleteItem.bind(this)}
		             />
		             <MenuItem primaryText="Help &amp; feedback" />
		             <MenuItem primaryText="Settings" />
		             <MenuItem primaryText="Sign out" />
		           </Menu>
				</Popover>
			</div>
		);
  }
}

export default Reminder;



