import React, {
  Component
} from 'react';

import {
  Router,
  Route,
  hashHistory,
  browserHistory
} from 'react-router';


// UI 
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import FontIcon from 'material-ui/FontIcon';
import {
  BottomNavigation,
  BottomNavigationItem
} from 'material-ui/BottomNavigation';
import AppBar from 'material-ui/AppBar';

import Paper from 'material-ui/Paper';
import IconLocationOn from 'material-ui/svg-icons/communication/location-on';

import {
  List,
  ListItem
} from 'material-ui/List';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentSend from 'material-ui/svg-icons/content/send';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import Divider from 'material-ui/Divider';
import ActionInfo from 'material-ui/svg-icons/action/info';

import FlatButton from 'material-ui/FlatButton';

import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';

import Drawer from 'material-ui/Drawer';

// 
import Reminder, {clearText} from './components/reminder';
import Favorites from './components/favorites';

import './font-icons/style.css';
import './App2.css';

const receipt = <FontIcon className="material-icons">receipt</FontIcon>;
const favoritesIcon = <FontIcon className="material-icons">favorite</FontIcon>;
const nearbyIcon = <IconLocationOn />;


const Logged = (props) => (
  <IconMenu
    {...props}
    iconButtonElement={
      <IconButton><MoreVertIcon /></IconButton>
    }
    targetOrigin={{horizontal: 'right', vertical: 'top'}}
    anchorOrigin={{horizontal: 'right', vertical: 'top'}}

    onItemTouchTap={onIconMenu}
  >
    <MenuItem primaryText="Clear" value='clear'/>
    <MenuItem primaryText="Drawer" value='drawer'/>
    <MenuItem primaryText="Sign out" />
  </IconMenu>
);

let onIconMenu = (e, item)=> {
  if(e.type == 'touchend') {
    let value = item.props.value;

    if(value == 'clear') {
      clearText();
    }
    else if(value == 'drawer'){
      _tabs.setState({
        open: !_tabs.state.open
      })
    }
  }
}

let _tabs;

class Tabs extends Component {

  hashMap = ['/', '/favorite']
  state = {
    selectedIndex: 0,
  };

  select = (index, hash) => {
    this.setState({
      selectedIndex: index
    });
    window.location.hash = hash;
  }

  constructor() {
    super();
    _tabs = this;
    var hash = window.location.hash.match(/#([^?]+)/);
    if (hash) {
      hash = hash[1];
    }

    var selectedIndex = this.hashMap.indexOf(hash);

    if (selectedIndex == -1) {
      selectedIndex = 0;
    }

    this.state.selectedIndex = selectedIndex;
    this.state.open = false;

    // 
    this.state.windowHeight = window.innerHeight;

    var _this = this;
    window.addEventListener('resize', () => {
      _this.setState({
        windowHeight: window.innerHeight
      });
    });
  }

  onLeftIconTap () {
    this.setState({
      open: !this.state.open
    })
  }
  render() {
    return (

      <Paper zDepth={1} className='paper' style={{height: this.state.windowHeight}}>
        <AppBar
           title="React1"
           onLeftIconButtonTouchTap={this.onLeftIconTap.bind(this)}
           iconElementRight={<Logged /> } 
         />
        <Drawer 
            open={this.state.open}
            docked={false} 
            onRequestChange={(open) => this.setState({open})} 
        >
            <MenuItem>Menu Item</MenuItem>
            <MenuItem>Menu Item 2</MenuItem>
        </Drawer>       

        <div className="main">
          <Router history={hashHistory}>
            <Route path="/" component={Reminder}/>
            <Route path="/favorite" component={Favorites}/>
          </Router>
        </div>

        <BottomNavigation selectedIndex={this.state.selectedIndex} className='bottom'>
          <BottomNavigationItem
            label=""
            icon={receipt}
            onTouchTap={() => this.select(0, '/')}
          />
          <BottomNavigationItem
            label="Favorites"
            icon={favoritesIcon}
            onTouchTap={() => this.select(1, '/favorite')}
          />
          <BottomNavigationItem
            label="Nearby"
            icon={nearbyIcon}
            onTouchTap={() => this.select(2)}
          />
        </BottomNavigation>
      </Paper>
    );
  }
}

export default Tabs;


